h1, .empty-cart {
    text-align: center;
}

.cart-item {
    text-align: center;
    border: 1px solid #ccc;
    margin: 2rem;
    padding: 1rem;

    .item-image {
        img {
            width: 50px;
        }
    }

    .name-price {
        h4 {
            margin: 0;
        }
        p {
            font-size: 1rem;
            font-weight: normal;
        }
    }

    .btns-container {
        button {
            border: none;
            padding: 0.5rem 1rem;
            cursor: pointer;
            font-size: 10px;
            margin-right: 5px;
            margin: 10px;
        }

        .btn-increase {
            color: #fff;
            background-color: #1a1a1a;
        }

        .btn-decrease, .btn-trash {
            color: #fff;
            background-color: #d9534f;
        }
    }

    .total-container {
        margin: 2rem;
        text-align: center;
    }
}

@media(min-width: 1024px) {
    .cart-page {
        display: flex;
        justify-content: center;

        .total-container {
            text-align: center;
        }
    }
    .cart-item {
        display: flex;
        justify-content: space-between;
        div {
            margin: 1rem;
        }
        .name-price {
            width: 200px;
        }
    }
}